import { _axios } from '@services';
var baseRoute = '/labels';
// POST
export var create_label = function (data) {
    return _axios({
        url: "".concat(baseRoute),
        method: 'POST',
        data: data,
    });
};
// GET
export var read_all_labels = function () {
    return _axios({
        url: "".concat(baseRoute),
        method: 'GET',
    });
};
export var read_label_by_id = function (labelId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(labelId),
        method: 'GET',
    });
};
// PATCH
export var update_label = function (labelId, data) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(labelId),
        method: 'PATCH',
        data: data,
    });
};
// DELETE
export var delete_label = function (labelId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(labelId),
        method: 'DELETE',
    });
};
