import { _axios } from '@services';
var baseRoute = '/milestones';
// POST
export var create_milestone = function (data) {
    return _axios({
        url: "".concat(baseRoute),
        method: 'POST',
        data: data,
    });
};
// GET
export var read_all_milestones = function () {
    return _axios({
        url: "".concat(baseRoute),
        method: 'GET',
    });
};
export var read_milestone_by_id = function (milestoneId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(milestoneId),
        method: 'GET',
    });
};
// PATCH
export var update_milestone = function (milestone, data) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(milestone),
        method: 'PATCH',
        data: data,
    });
};
export var update_milestone_status = function (milestoneId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(milestoneId, "/status"),
        method: 'PATCH',
    });
};
// DELETE
export var delete_milestone = function (milestoneId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(milestoneId),
        method: 'DELETE',
    });
};
