import { _axios } from '@services';
var baseRoute = '/issues';
// POST
export var create_issue = function (data) {
    return _axios({
        url: "".concat(baseRoute),
        method: 'POST',
        data: data,
    });
};
export var create_issue_comment = function (issueId, data) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(issueId, "/comment"),
        method: 'POST',
        data: data,
    });
};
// GET
export var read_all_issues = function () {
    return _axios({
        url: "".concat(baseRoute),
        method: 'GET',
    });
};
export var read_issue_by_id = function (issueId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(issueId),
        method: 'GET',
    });
};
// PATCH
export var update_issue = function (issueId, data) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(issueId),
        method: 'PATCH',
        data: data,
    });
};
export var update_issue_title = function (issueId, data) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(issueId),
        method: 'PATCH',
        data: data,
    });
};
export var update_issue_status = function (issueId, status) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(issueId, "/status"),
        method: 'PATCH',
        data: { status: status },
    });
};
// DELETE
export var delete_issue = function (issueId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(issueId),
        method: 'DELETE',
    });
};
export var delete_issue_comment = function (issueId, commentId) {
    return _axios({
        url: "".concat(baseRoute, "/").concat(issueId, "/comment/").concat(commentId),
        method: 'DELETE',
    });
};
