import { _axios } from '@services';
var baseRoute = '/users';
// POST
export var register_user = function (data) {
    return _axios({
        url: "".concat(baseRoute, "/register"),
        method: 'POST',
        data: data,
    });
};
export var login_user = function (data) {
    return _axios({
        url: "".concat(baseRoute, "/login"),
        method: 'POST',
        data: data,
    });
};
export var github_login = function () {
    return _axios({
        url: "/auth/github/callback",
        method: 'GET',
    });
};
// GET
export var read_all_users = function () {
    return _axios({
        url: "".concat(baseRoute),
        method: 'GET',
    });
};
// PUT
// DELELE
export var withdrawal_user = function () {
    return _axios({
        url: "".concat(baseRoute, "/withdrawal"),
        method: 'DELETE',
    });
};
