/**
 * 동적으로 변하는 new Date()는 인수로 받아오는게 바람직하다.
 * @param target string | number | Date
 * @param now string | number | Date; default new Date()
 * @returns 1초 전, 1분 전, 1시간 전, 1일 전, 1년 전
 */
export var toTimeDuration = function (target, now) {
    if (now === void 0) { now = new Date(); }
    var second = Math.floor((new Date(now).getTime() - new Date(target).getTime()) / 1000);
    if (second < 60)
        return "".concat(second, "\uCD08 \uC804");
    var minute = Math.floor(second / 60);
    if (minute < 60)
        return "".concat(minute, "\uBD84 \uC804");
    var hour = Math.floor(second / 3600);
    if (hour < 24)
        return "".concat(hour, "\uC2DC\uAC04 \uC804");
    var day = Math.floor(hour / 24);
    if (day < 365)
        return "".concat(day, "\uC77C \uC804");
    return "".concat(Math.floor(day / 365), "\uB144 \uC804");
};
